@import "src/static/styles/base.scss";

.AppBox {
  background-position: center 0;
  background-size: cover;
  min-height: 150vh;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.bg {
  background-image: url(./static/imgs/bg.png);
}
.bg0 {
  background-image: url(./static/imgs/bg0.webp);
}
.bg1 {
  background-image: url(./static/imgs/bg1.webp);
}
.bg2 {
  background-image: url(./static/imgs/bg2.webp);
}

.main-color {
  background-color: $themeColor;
}

.secondary-color {
  background-color: $themeColor1;
}
